import { Component, OnInit, Inject, Optional } from '@angular/core';

import { MatDialogRef } from "@angular/material/dialog";
import { MAT_DIALOG_DATA } from '@angular/material/dialog';

import { ModalsService } from '../modals.service';


@Component({
  selector: 'app-delete-confirm-modal',
  templateUrl: './delete-confirm-modal.component.html',
  styleUrls: ['./delete-confirm-modal.component.scss']
})
export class DeleteConfirmModalComponent implements OnInit {

  constructor(
    @Optional() @Inject(MAT_DIALOG_DATA) public id: any,
  	private confirmDialogRef: MatDialogRef<DeleteConfirmModalComponent>,
    private modalsService:ModalsService
  ) { }

  close() {
    this.confirmDialogRef.close(false);
  }

  deleteItem() {
    this.modalsService.setDeleteIndex(this.id);
    this.confirmDialogRef.close(true);
  }

  ngOnInit(): void {
  }

}
